<template>
  <div>
    <page-title title="MQTT Console"/>
    <v-row>
      <v-col cols="12" class="text-left">
        <v-btn x-small class="mr-1 btn-height-22 white--text mr-1" color="primary" @click="importDialog = true">Import<v-icon x-small right>mdi-import</v-icon></v-btn>
        <v-btn x-small class="mr-1 btn-height-22 white--text mr-1" color="exportColor" @click="clickJsonExport">Export<v-icon x-small right>mdi-export</v-icon></v-btn>
      </v-col>
    </v-row>
    <mqtt-console :subscriptions="subscriptions"></mqtt-console>
    <v-dialog v-model="importDialog" max-width="500px">
      <import-json-form :closeDialogImport="closeDialogImport" :saveImport="saveImport"></import-json-form>
    </v-dialog>
  </div>
</template>

<script>

import MqttConsole from "./MqttConsole";
import ImportJsonForm from '../Common/ImportJsonForm';
import PageTitle from '../Common/PageTitle.vue';

import { copyToClipboard } from '../../utils/utils';
import axios from '../../axios';

export default {
  components: {
    MqttConsole, ImportJsonForm, PageTitle
  },
  data: () => ({
    importDialog: false,
  }),
  computed: {
    mqttConfig() {
      return this.$store.state.mqttConfig;
    },
    subscriptions() {
      const { subscriptions } = this.mqttConfig;
      return subscriptions || [];
    }
  },
  methods: {
    clickJsonExport(){
      const exportData = JSON.stringify(this.subscriptions);
      const status = copyToClipboard(exportData);
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    closeDialogImport(){
      this.importDialog = false;
    },
    async saveImport(data){
      this.mqttConfig.subscriptions = JSON.parse(data);
      this.onSave();
      this.closeDialogImport();
    },
    onSave() {
      axios.put('mqtt/config', this.mqttConfig).then(r => {
        this.$toasted.show('Success');
        this.$store.commit('changeMqttConfig', this.mqttConfig);
      });
    },
  }
};
</script>
